.dialog {
    position: fixed;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -30%);
    background-color: white;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.75);
    z-index: 1000;
    width: 300px;
    text-align: center;
  }
  
  .close-button {
    position: absolute;
    top: 5px;
    right: 10px;
    background: none;
    border: none;
    font-size: 18px;
    cursor: pointer;
    color: rgb(9, 0, 0);

  }
  
  .go-button {
    padding: 10px 20px;
    margin: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .go-button:hover {
    background-color: #0056b3;
  }
  /* Movie.css */

/* General Styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 20px;
    background-color: #f4f4f4;
}

h2 {
    text-align: center;
    color: #333;
}

/* Styles for the Movie List */
ul {
    list-style-type: none;
    padding: 0;
}


li:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Thumbnail image styles */
img {
    width: 100px;
    height: auto;
    border-radius: 4px;
    margin-right: 15px;
}

/* Button Styles */
.play-button {
    background-color: #007BFF;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
}

.play-button:hover {
    background-color: #0056b3;
}

/* Video Player Styles */
.video-player {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}

/* Responsive Styles */
@media (max-width: 768px) {
    li {
        flex-direction: column;
        align-items: flex-start;
    }

    img {
        margin-bottom: 10px;
    }
}

@media (max-width: 480px) {
    h2 {
        font-size: 1.5em;
    }

    .play-button {
        width: 100%;
        padding: 12px;
        text-align: center;
    }

    .video-player iframe {
        width: 100%;
        height: auto;
    }
}
